.thumnailViewSet{
	overflow:hidden;
	position:relative;
	.left{
		@include breakpoint($md){
			float:left;
			width:40%;
		}
		.thumCtrl{
				position:relative;
				padding:0 40px;
				margin:10px 0;
			@include breakpoint($md){
				margin:0;
				width:56%;
				position:absolute;
				right:0;
				bottom:0;
			}
			.pop-prev,
			.pop-next{
				position:absolute;
				top:50%;
				font-size:36px;
				line-height:1;
				padding:0;
				margin:0;
				margin-top:-20px;
			}
			.pop-prev{
				left:-3px;
				background-color:transparent;
				&:before {
					content: '';
					font-size:36px;
					font-family:'LineAwesome';
					color:#344891;
				}
			}
			.pop-next{
				right:-3px;
				background-color:transparent;
				&:before {
					content: '';
					font-size:36px;
					font-family:'LineAwesome';
					color:#344891;
				}
			}
		}
	}
	.right{
		@include breakpoint($md){
			float:right;
			width:56%;
			padding-bottom:96px+10;
		}
		.subject{
			font-size:22px;
		}
		.list{
			margin-left:0;
			>li{
				margin-top:10px;
			}
		}
	}
}

.thumCtrlList{
	.owl-item{
		a{
			display:block;
			width:150px;
			
			border:1px solid $bdcolor;
			overflow:hidden;
		}
		img{
			height:96px;
		}
	}
	.owl-dots{display:none;}
}
.lageImg{
	display:block;
	position:relative;
	img{vertical-align:top;}
	&:after{
		content: '';
		font-family:'LineAwesome';
		display:block;
		position:absolute;
		right:0;
		bottom:0;
		width:60px;
		height:60px;
		text-align:center;
		background-color:rgba(0,0,0, 0.75);
		color:#fff;
		padding-top:7px;
		font-size:30px;
		line-height:1;
		vertical-align:top;
		
	}
	&:before{
		content: '크게';
		text-align:center;
		position:absolute;
		right:0;
		bottom:9px;
		width:60px;
		text-align:center;
		z-index:2;
		color:#fff;
	}
}
.div.pp_default .pp_expand{
	@include breakpoint($sm){
		display:none;
	}
}