.sh{
	background-color:#f1f3f6;
	border:1px solid #424f88;
	padding:5px;
	padding:19px;
	@include breakpoint($md){
	}
	.set{
		display:table;
		width:100%;
		>*{
			display:block;
			padding:5px 0;
			vertical-align:middle;
			@include breakpoint($sm){
				padding:0;
				margin:5px 0;
				&.btn,
				&.total{display:inline;margin-right:5px;
					button{
						padding-left:14px;
						padding-right:14px;
					}
				}

			}
			@include breakpoint($md){
				vertical-align:top;
				display:table-cell;
				padding:0 1px;
			}
		}
		>span>*{
			@include breakpoint($md){
				width:100%;
			}
		}
	}
	&.line .set{
		.select{width:auto;select{width:auto;min-width:120px;margin-left:10px;}}
	}
	.select{
		@include breakpoint($md){
			width:150px
		}
	}
	.keyword{
		input[type=text]{width:100%;}
	}
	.btn{
		// width:100%;
		@include breakpoint($md){
			width:100px;
		}
	}
	.total{width:100px;}
	&.multi{
		.set{
			>.title{
				@include breakpoint($md){
					width:100px;
					line-height:40px;
					text-align:center;
				}
			}
			>.select select{width:100%;}
			>.wa{
				input{
					width:auto;
				}
			}
			&+.set{
				margin-top:5px;
			}

			.chklist{
				line-height:40px;
				label{
					display:block;width:50%;float:left;
					@include breakpoint($md){
						display:inline-block;width:auto;float:none;
						min-width:140px;
					}
					@include breakpoint($xl){
						min-width:inherit;
						&+label{margin-left:5px;}
					}
				}
			}
			&.selectlist{
				@include breakpoint($md){
					width:auto;
					>select+.title{padding-left:20px;width:auto;padding-right:20px;}

				}
			}
		}
	}
	&.type-radio{
		position:relative;
		label{
			display:block;
			position:relative;
			padding-left:30px;
			input{position:absolute;left:0;top:3px;}
		}
		.btn{
			width:auto;
			display:block;
			margin-top:10px;
			text-align:center;
			.b-sh{width:130px;}
		}
		@include breakpoint($md){
			.s{
				overflow:hidden;
				label{
					float:left;width:50%;
				}
			}
		}
		@include breakpoint($xl){
			.s{
				padding-right:120px;
				margin-bottom:-3px;
				label{margin-bottom:3px;}
			}
			.btn{
				position:absolute;
				right:20px;
				top:50%;
				margin-top:-20px;
			}
		}

	}
}
.sh-1{
	@extend .sh;
	@include breakpoint($md){
		.select{
			width:15%;
		}
	}
}
.sh-2{
	@extend .sh;
	@include breakpoint($md){
		.select{
			width:30%;
			position:relative;
			left:-2px;
			select{
				float:left;
				width:50%;
			}
			select+select{position:relative;right:-2px;}
		}
	}
}
.sh-facilities{
	@extend .sh;
	.set{
		.title{
			display:none;
			@include breakpoint($md){
				display:table-cell;
				width:83px;
				vertical-align:middle;
			}
		}
		.labelList{
			line-height:40px;
			margin-top:-10px;
			@include breakpoint($md){
				margin-top:0;
			}
			label{
				white-space:nowrap;
				min-width:102px;
				input{margin-right:8px;}
				@include breakpoint($md){
					margin-right:25px;
					&:last-child{margin-right:0;}
				}
			}
		}
		&+.set{
			margin-top:5px;
			@include breakpoint($md){
				margin-top:10px;
			}
		}
	}
}
.sh-s{
	@extend .sh;
	.set{
		width:auto;
		>*{}
	}
}