#footer{
	clear:both;
	padding-top:40px;
	.favSiteServiceSet{
		text-align:center;
		margin-top:45px;
		margin-bottom:20px;
		.toggle{
			display:inline-block;
			margin: 0 auto;
			&:after {
				content: '';
				font-family:'FontAwesome';
				font-size:24px;
				width:39px;
				text-align:right;
				display:inline-block;
			}
		}
	}
	.ft{
		padding:0;
		@include breakpoint($lg){
			background-color:#f5f5f5;
			padding:20px 0;
		}
		.set{
			margin: 0 auto;
			max-width:$mw;
			@include breakpoint($lg){
				height:40px;
			}
		}
		#footerAd{
			background-color:#f5f5f5;
			padding:20px 11px;
			padding-left:140px;
			height:80px;
			position:relative;
			width:100%;
			@include breakpoint($lg){
				float:left;
				height:60px;
				padding-top:0;
				padding-bottom:0;
				width:70%;
			}
			.footerAd-ctrl{
				position:absolute;
				z-index:2;
				left:50px;
				@include breakpoint($lg){
					left:40px;
				}
				button{
					width:40px;
					height:40px;
					float:left;
					background-size:40px;
					background-color:transparent;
					span{position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;clip:rect(0,0,0,0);border:0;}
					&.footerAd_pre{background-image:url(/static/health/img/common/footerAd-ctrl-left.png);}
					&.footerAd_play{background-image:url(/static/health/img/common/footerAd-ctrl-play.png);}
					&.footerAd_stop{background-image:url(/static/health/img/common/footerAd-ctrl-stop.png);display:none;}
					&.footerAd_next{background-image:url(/static/health/img/common/footerAd-ctrl-right.png);}
				}
			}
			.owl-nav{
				position:absolute;
				left:-130px;
				top:0;
				@include breakpoint($lg){
					left:-140px;
				}
				button{
					width:40px;
					height:40px;
					float:left;
					background-size:40px;
					background-color:transparent;
					span{position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;clip:rect(0,0,0,0);border:0;}
					&.owl-prev{background-image:url(/static/health/img/common/footerAd-ctrl-left.png);}
					&.owl-next{background-image:url(/static/health/img/common/footerAd-ctrl-right.png);margin-left:40px;}
				}
			}
			.owl-dots{display:none;}
			.footerAd-list{
				// float:left;
				.item{text-align:center;
					a{display:inline-block;}
				}
				.owl-nav{float:left;
				}
			}
		}
		.fsite{
			margin:11px;
			margin-bottom:0;
			div{
				margin-bottom:2px;
				position:relative;
				padding-right:100px+2;
				select{
					width:100%;
					height:40px;
					border:1px solid #b8c4e4;
					line-height:40px;
					padding-left:20px;
					background-color:#fff;
					position:relative;
				}
				button{
					position:absolute;
					right:0;
					top:0;
					margin-left:2px;
					height:40px;
					width:100px;
					background-color:#20409a;
					color:#fff;
					text-align:center;
				}
			}
			@include breakpoint($lg){
				margin:0;
				float:right;
				div{
					float:left;
					padding-right:0;
					margin-left:2px;
					select{
						width:170px;
					}
					button{
						position:static;
					}
				}
			}
			*{vertical-align:top;}
		}
	}
	.footerSet{
		position:relative;
		max-width:$mw;
			margin: 0 11px;
			padding:20px 0 0 0;
		@include breakpoint($lg){
			margin: 0 auto;
			padding:25px 0 0 0;
		}
		nav{
			@include breakpoint($md){margin-bottom:15px;}
			margin-left:-11px;
			@include clearfix;
			ul>li:first-child a{color:$color_red;}
			a{
				float:left;
				position:relative;
				color:#242424;
				padding:0 11px;
				font-size:15px;
				// font-weight:bold;
				line-height:1.9;
				@include breakpoint($lg){
					line-height:1;
				}
				&::before {
					content: '';
					position:absolute;
					left:0;
					top:8px;
					height:15px;
					margin-top:-8px;
					border-left:1px solid #e3e3e3;
				}
				&:first-child:before{display:none;}
			}
		}
		address{
			line-height:1.71;
			//font-size:14px;
			color:#444;
			font-style:normal;
			letter-spacing:-0.8px;
			padding-bottom:25px;
			p{
				margin-bottom:10px;
			}
		}
		// .markset{
		// 	display:none;
		// 	@include breakpoint($lg){
		// 		display:block;
		// 		position:absolute;
		// 		right:0;
		// 		top:20px;
		// 		padding:18px 0 0 0;
		// 		img{float:left;margin-left:22px;height:60px;}
		// 	}
		// }
		.ftset{position:inherit;top:inherit;right:inherit;width:100%;
			@include breakpoint($xl){
				position:absolute;top:20px;right:0;width:480px;text-align:right;
			}
			.ftcon1{overflow:hidden;margin:10px 0;
				@include breakpoint($xl){
					margin:0;
				}
				.newslt{display:none;
					.inp{width:250px;vertical-align:top;}
					button{lineh-height:40px;height:40px;background:#7b7a79;color:#fff}
					@include breakpoint($md){
						float:left;vertical-align:top;display:block;
					}
				}
				@include breakpoint($sm){
					text-align:center;
				}
			}
			.ftcon2{display:none;
				.fmenu{float:left;line-height:18px;
					a{display:inline-block}
					.f1{
						&:before{display:inline-block;content:"\f2b1";padding-right:3px;font-family:"LineAwesome";font-size:21px;}
					}
					.f2{margin-left:20px;
						&:before{display:inline-block;content:"\f2e3";padding-right:3px;font-family:"LineAwesome";font-size:21px;}
					}
				}
				.markset{float:right;
					img{float:left;margin-left:10px;height:48px;}
				}
				@include breakpoint($xl){
					overflow:hidden;margin-top:10px;display:block;
				}
			}
		}
	}
}
.snsSiteSet{
	@include breakpoint($xl){
		float:right;
	}
	float:left;
	@include breakpoint($sm){
		float:none;display:inline-block;
	}
	a{
		float:left;
		width:32px;
		height:32px;
		background-size:100%;
		margin-left:4px;
		&.btn-tweeter{
			background-image:url(/static/portal/img/common/btn-tweeter.png);
		}
		&.btn-facebook{
			background-image:url(/static/portal/img/common/btn-facebook.png);
		}
		&.btn-blog{
			background-image:url(/static/portal/img/common/btn-blog.png);
		}
		&.btn-instagram{
			background-image:url(/static/health/img/common/btn-instagram.png);
		}
	}
}