.joinStageStep{
	border:1px solid $bdcolor;
	color:#757575;
	display:table;
	width:100%;
	text-align:center;
	table-layout:fixed;
	font-size:18px;
	margin-bottom:20px;
	@include breakpoint($sm){
		text-align:left;
		padding:18px 20px;
		background-color:#f1f2f7;
		li{
			display:none;
			&.on{
				display:block;
				&:after{display:none}
				span{
					font-size:18px;
					color:#f76627;
					line-height:28px;
					display:inline-block;
					&:before {
						content: '';
						font-family:'FontAwesome';
						font-size:28px;
						margin-right:15px;
						display:inline-block;
						vertical-align:top;
					}
				}
			}
		}
		li+li{
			position:relative;
			&:after {
				content: '';
				font-family:'FontAwesome';
				position:absolute;
				font-size:18px;
				left:0;
				top:50%;
				margin-top:-7px;
				line-height:1;
			}
		}
	}
	@include breakpoint($md){
		li{
			// display:table-cell;
			float:left;
			padding:26px 3.2%;
			@include breakpoint($xl){padding:26px 5.2%;}
			vertical-align:middle;
			&.on{
				
				span{
					font-size:24px;
					color:#f76627;
					line-height:26px;
					display:inline-block;
					&:before {
						content: '';
						font-family:'FontAwesome';
						font-size:26px;
						margin-right:5px;
					}
				}
			}
		}
		li+li{
			position:relative;
			&:after {
				content: '';
				font-family:'FontAwesome';
				position:absolute;
				font-size:18px;
				left:-7px;
				top:50%;
				margin-top:-7px;
				line-height:1;
			}
		}
	}
}
.joinstep-1{
	margin-bottom:20px;
	&+p{margin:0 20px;}
}
.joinBox-type1{
	h2{
		font-size:18px;
		color:#fff;
		padding:22px 0;
		text-align:center;
		background-color:#344891;
		font-weight:normal;
	}
	.body{
		border:1px solid $bdcolor;
		border-top:none;
		padding:20px;
		p{margin-bottom:20px;}
		a{
			display:inline-block;
			margin: 0 auto;
			padding:18px 38px;
			text-align:center;
			color:#fff;
			background-color:#424f88;
		}
	}
}
.joinBox-type2{
	@extend .joinBox-type1;
	h2{background-color:#787e96;}
	.body{
		a{background-color:#5e5f67;}
	}
}

.agg-set{
	margin: 0 0 20px;
	padding: 30px;
	border:1px solid #e1e1e1;
	h2,
	h3,
	h4{
		font-size:1.2em;
		margin:0 0 16px;
		font-weight:normal;
	}
	.set{
		height:152px;
		border:1px solid $bdcolor;
		overflow:scroll;
		overflow-x:hidden;
		padding:20px;
		background-color:#f1f3f6;
		margin-bottom:20px;
		>*{font-size:1em;line-height:1.6;}
		&.auto{height:auto;overflow:auto;}
	}
	label strong{color:$color_orange;}
}
.aggtotal{
	text-align:center;
	label{
		font-size:14px;
		font-weight:bold !important;
	}
}

/* 회원가입완료 **************************************************/
.joinstep-end{
	background: url(/static/portal/img/sub/box-pt.png) 0 0 repeat;
	background-size:8px;
	border:1px solid $bdcolor;
	padding:48px 0;
	p{
		font-size:18px;
		padding-top:102px;
		background: url(/static/portal/img/sub/joinstep-end.png) center 0 no-repeat;
		background-size:73px;
		text-align:center;
		em{
			color:#344891;
		}
	}
	.btnSet{margin-top:20px;margin-bottom:0;}
}
.idfind{
	@extend .joinstep-end;
	p{
		padding:81px 0 0 0;
		background-image:url(/static/portal/img/sub/idfind-icon.png);
		background-size:131px;
		color:#344891;
		font-size:1.2em;
		strong{
			color:#141414;
			border:1px solid #7f7f7f;
			background-color:#f2f3f5;
			padding:8px 10px;
			display:inline-block;
			line-height:1;
			font-size:0.83em;
		}
	}
	p+p{
		border-top:1px solid $bdcolor;
		margin-top:10px;
		padding:18px 0 0 0;
		font-size:1em;
		background:none;
		color:$color_orange;
	}
}
.memberUpdate{
	@extend .idfind;
	p{
		background-image:url(/static/portal/img/sub/200914.png);
		background-size:82px;
		padding-top:100px;
	}
	p+p{
		border:none;
		padding:0;
	}

}