*{box-sizing: border-box;}
img{max-width:100%;vertical-align:top;}
p{letter-spacing:-0.03em}
body{
	font-family:$fontSet;
	color:$color_default;
	font-size:$defaultfontsize;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow-x:hidden;
	line-height:1.7;
}
.keyboardControlActive{
	transform:translate3d(0, 0, 0) !important;
}
/* 자료담당자 */
.pageAdm{
	margin-top:20px;
	background-color:#777c8a;
	@include breakpoint($lg){
		display:table;
		width:100%;
		table-layout:fixed;
	}
	p{
		color:#fff;
		padding:8px 0;
		line-height:2;
		&:first-child{
			padding-top:15px;
		}
		&+p{
			padding-left:70px;
			border-top:1px solid #acaeb5;
		}
		@include breakpoint($lg){
			padding:15px 0;
			display:table-cell;
			&+p{
				padding-left:0;
				text-align:center;
				width:25%;
				border-left:1px solid #fff;
			}
		}
		&:first-child{
			padding-left:70px;
			background: url(/static/portal/img/common/pageAdm-bu.png) 29px 15px no-repeat;
			background-size:30px;
		}
	}
}
/* 한줄의견 **************************************************/
.pageComment{
	margin-top:20px;
	padding:19px 29px;
	border:1px solid #dadada;
	background:#fff url(/static/portal/img/common/pageComent-pt.png) repeat;
	.pageAdm+&{border-top:none;margin-top:0;}
	input{
		width:100%;
		height:40px;
	}
	textarea{
		width:100%;
		height:60px;
		@include breakpoint($lg){height:40px;line-height:36px;text-indent:10px;}
	}
	.pageCommentName{width:100px;@include breakpoint($lg){width:100%;}}
	button{
		color:#fff;
		background-color:#f76627;
		@include breakpoint($lg){width:100%;}
		height:40px;
	}
	.row{
		>div{
			margin-bottom:2px;
			&+div{margin-bottom:10px;}
			@include breakpoint($lg){
				margin-bottom:0;
				&+div{margin-bottom:0;}
			}
		}
	}

}

// 과 이용안내
.operationGuide{
	border:4px solid #22b24c;
	overflow:hidden;
	padding:26px;
	dl{
		&:first-child{
			border-bottom:1px solid #c5decc;
			padding:0 0 17px;
			margin-bottom:17px;
		}
		dt{
			color:#16953b;
			border-right:1px solid #c5decc;
			float:left;
			// width:88px;
			padding-left:2px;
			padding-right:20px;
			margin-right:20px;
			@include breakpoint($sm){
				min-width:88px;
				padding-right:0;
			}
		}
		dd{}
		[class*=ico-]{
			margin-right:6px;
			color:#1f497d;
		}
	}
	dl+dl{
		@include breakpoint($sm){
			margin-top:12px;
		}
		@include breakpoint($md){
			float:left;
			width:33.33%;
		}
	}
	&.l{
		dt{width:116px;}
		dl{width:100%;}
		dl+dl{margin-top:10px;}
		dl:first-child+dl{margin-top:0;}
	}
}
// 관련사이트
.aside_site{
	margin:30px 0 15px;
	padding:20px;
	background-color:#f0f2f7;
	>h2{
		font-size:16px;
		padding-left:24px;
		background: url(/static/portal/img/common/ico-relationSite.png) 0 center no-repeat;
		background-size:14px;
		margin-bottom:5px;
	}
	ul{
		overflow:hidden;
		margin:0 -5px;
		li{
			margin:2px 0;
			@include breakpoint($lg){
				margin:5px 0;
				float:left;
				padding:0 5px;
				width:33.33%;
			}
			a{
				display:block;
				background-color:#fff;
				border:1px solid #dee0e6;
				padding:9px 0;
				text-align:center;
			}
		}
	}
}

@include breakpoint($sm){
	.iv{
		overflow-x:auto;
		// div{min-width:620px;}
		position:relative;
		img{vertical-align:top;}
		&:after{
			content: '';
			font-family:'LineAwesome';
			display:block;
			position:absolute;
			right:10px;
			top:10px;
			// bottom:0;
			width:60px;
			height:60px;
			text-align:center;
			background-color:rgba(0,0,0, 0.75);
			color:#fff;
			padding-top:7px;
			font-size:30px;
			line-height:1;
			vertical-align:top;
			border-radius:25px;
			
		}
		&:before{
			content: '원본';
			text-align:center;
			position:absolute;
			// right:0;
			// bottom:9px;
			right:10px;
			top:46px;
			width:60px;
			text-align:center;
			z-index:2;
			color:#fff;
		}
	}
}

// 
.iframe{
	width:100%;
	height:500px;
}

.dllist{
	dl{
		display:table-row;
		>*{
			display:table-cell;
			padding-bottom:10px;
		}
	}
	dt{
		white-space:nowrap;
		color:#424f88;
		&:before {
			content: '';
			width:4px;
			height:4px;
			display:inline-block;
			overflow:hidden;
			border-radius:50%;
			background-color:#b8b8b8;
			vertical-align:middle;
			margin-right:6px;
		}
		position:relative;
		padding-right:8px;
		&:after {
			content: ':';
			position:absolute;
			right:0;
			top:0;
		}
	}
	dd{
		padding-left:10px;
	}
	.bu2+&{margin-left:20px;}
}
// 순서도
.arrbox{
	border:1px solid $bdcolor;
	border-radius:3px;
	padding:29px;
	.subject{
		margin-bottom:20px;
		line-height:1;
		color:#20409a;
		text-align:center;
	}
	.step-arr{
		margin-left:0;
	}
}
.step-arr{
	@include breakpoint($md){
		margin-left:$m1;
	}
	.list &{margin-left:0;margin-top:10px;}
	&.h dl dd{height:auto;padding-top:20px;padding-bottom:20px;}

	&.noarr{
		.s > li:before,
		.s > div:before,
		.s>*>*:before{display:none;}
	}
	.s{
		@include breakpoint($md){
			margin:0 -15px;
		}
		text-align:center;
		// @include clearfix;
		display:inline-block;
		width:100%;
		>li,
		>div{
			position:relative;
			margin-bottom:30px;
			@include breakpoint($md){
				margin-bottom:0;
				float:left;
				width:20%;
				padding:0 15px;
			}
			&:last-child{
				margin-bottom:0;
				&:before{display:none;}
			}
			&:before {
				content: '';
				font-family:'LineAwesome';
				font-size:24px;
				color:#000;
				position:absolute;
				right:-15px;
				bottom:50%;
				margin-bottom:-12px;
				width:30px;
				@include breakpoint($sm){
					right:50%;
					margin-right:-15px;
					margin-bottom:0;
					bottom:-30px;
					transform:rotate(90deg);
				}
			}

		}
	}
	.d,
	dl{
		position:relative;
		// padding-top:45px;
		dt{
			position:relative;
			z-index:1;
			margin-bottom:-3px;
			padding:14px 0;
			width:100%;
			border-radius:3px;
			background-color:#6274b2;
			color:#fff;
			line-height:1.3;
			span{
				color:#81fea6;
			}
		}
		dd{
			height:80px;
			padding:5px;
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			border:1px solid $bdcolor;
			border-top:none;
		}
	}
	&.type2{
		dl{
			dt{
				height:66px;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
			}
			dd{}
		}
	}
	&.one{
		.s{
			>li:before,
			>div:before{
				@include breakpoint($md){margin-bottom:-20px;}
			}
			>li>*,
			>div>*{
				// border:1px solid $bdcolor;
				// background-color:#ebeff8;
				background-color:#6274b2;
				color:#fff;
				text-align:center;
				padding:55px 5;
				border-radius:3px;
			}
		}
		&.l1{
			.s>li>*{
				height:80px;padding:5px;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
			}
		}
		&.l3{
			.s>li>*{height:150px;padding:5px;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
			}
		}
		&.inIcon{
			.s>li>*{
				span{display:block;padding-bottom:8px;}
				height:115px;
				padding:5px;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
			}
		}
	}
	&.size6{
		.s{
			>li,
			>div{
				@include breakpoint($md){
					float:left;
					width:33.33%;
					margin-bottom:0;
					&.i1,
					&.i2,
					&.i3{
						margin-bottom:30px;
					}
					&.i3:before{
						right:50%;
						margin-right:-15px;
						margin-bottom:0;
						bottom:-30px;
						transform:rotate(90deg);
					}
					&.i4:before,
					&.i5:before,
					&.i6:before{
						right:100%;
						margin-right:-15px;
						transform:rotate(180deg);
					}
					&.i4,
					&.i5,
					&.i6{
						float:right;
					}
				}
			}
		}
	}
	&.size5{
		.s{ >li,
			>div{
				@include breakpoint($md){
					&.i1, &.i2, &.i3, &.i4, &.i5 {margin-bottom:30px}
					&.i5:before{right:50%; margin-right:-15px; margin-bottom:0; bottom:-30px; transform:rotate(90deg); }
					&.i6:before,
					&.i7:before,
					&.i8:before,
					&.i9:before{right:100%; margin-right:-15px; transform:rotate(180deg)}
					&.i6, &.i7, &.i8, &.i9 {float:right}
				}
			}
		}
	}
	&.size3{
		.s{
			>li,
			div{
				@include breakpoint($md){
					width:33.33%;
				}
			}
		}
	}
	&.size4{
		.s{
			@include breakpoint($md){
				margin-bottom:-20px;
			}
			>li,
			>div{
				@include breakpoint($md){
					width:25%;

					margin-bottom:30px;
					&.i8:before,
					&.i4:before{
						right:50%;
						margin-right:-15px;
						margin-bottom:0;
						bottom:-30px;
						transform:rotate(90deg);
					}
					&.i5:before,
					&.i6:before,
					&.i7:before
					{
						right:100%;
						margin-right:-15px;
						transform:rotate(180deg);
					}
					&.i5,
					&.i6,
					&.i7{
						float:right;
					}
					&.i8{}
				}
				div{width:auto;float:none;}
			}
		}
	}
	&.listIn{
		dl dd{
			display:block;
			height:auto;
			text-align:left;
			padding:15px 10px;
			.bu3{margin-left:0;}
			.list{margin-left:0;}
		}
		div{
			p{
				// height:80px;
				padding:5px;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
				border-radius:3px;
				background-color:#6274b2;
				color:#fff;
				
			}
		}
	}
	&.vam dl dd{
		display: flex;
		flex-direction: row;
		justify-content: left;
		align-items: center;
		@include breakpoint($sm){
			height:auto !important;
		}
	}
}

// 연혁
.historySet{
	margin-top:30px;
	>div{
		display:none;
		&.on{display:block;}
	}
	dl{
		padding:23px 0;
		overflow:hidden;
		border-bottom:1px solid $bdcolor;
		display:table;
		width:100%;
		dt,
		dd{
			display:table-cell;
			vertical-align:top;
		}
		dt{
				width:20%;
			@include breakpoint($md){
				text-align:center;
				width:24%;
			}
			@include breakpoint($lg){
				width:28%;
			}
			font-family:'Noto-Regular';
			font-style:italic;
			line-height:1;
			.yy{
				font-size:22px;
				margin-right:10px;
				@include breakpoint($md){
					font-size:36px;
				}
				@include breakpoint($lg){
					margin-right:21%;
				}

			}
			.dd{
				font-size:18px;
				color:#20409a;
				margin-top:5px;
				@include breakpoint($lg){
					width:60px;
				}
			}
			span{vertical-align:top;display:inline-block;}
		}
		dd{
			@include breakpoint($md){
				// float:left;				
			}
			@include breakpoint($lg){
				width:78%;
			}
			line-height:2;
			p{
				text-indent:-12px;
				padding-left:12px;
			}
		}

	}
}
.historySet2{
	border-top:1px solid $bdcolor;
	dl{
		border-bottom:1px solid $bdcolor;
		padding:20px 0;
		dt{
		}
		dd{
			li{
				margin:6px 0;
				text-indent:-74px;
				padding-left:74px;
				*{text-indent:0;}
			}
		}
		@include breakpoint($md){
			display:table;
			width:100%;
			table-layout:fixed;
			>*{display:table-cell;vertical-align:top;}
			dt{
				width:150px;
				text-align:right;
				padding-right:40px;
			}
			dd{}
		}
	}

	.yy{
		font-style:italic;
		letter-spacing:-2px;
		display:block;
		font-size:22px;
		line-height:1;
		@include breakpoint($md){
			font-size:36px;
			font-family:'Noto-Regular';
		}
		@include breakpoint($lg){

		}

	}
	.dd{
		font-style:italic;
		display:inline-block;
		width:84px;
		font-size:18px;
		color:#20409a;
		@include breakpoint($md){
			font-family:'Noto-Regular';
		}
	}
}

