*{box-sizing: border-box}
button{background-color:transparent;}
a{color:#141414;}
a[href^="tel"] {
  color: inherit; /* Inherit text color of parent element. */
  text-decoration: none; /* Remove underline. */
}

$mw:1276px;

$m1:24px;
$m2:34px;

$sm                 : max-width 767px;
$smd                : max-width 991px;
$md                 : 768px;
$lg                 : 1276px;
$lg                 : 992px;
$xl                 : 1276px;

// board list color
$bdColor            : #dedede;
$bdtopcolor         : #424f88;
$linkColor          : #000;
$cellhovercolor		: #f4f4f4;

// board view color
