$color_deepgray     : #333;
$color_orange       : #f76627;
$color_blue         : #20409a;
$color_cobalt       : #02728D;
$color_red          : #c10101;
$color_green        : #3A7525;

$btnbgcolor:#6274b2;

$defaultfontsize : 15px;

/* font color */
.col_black    {color : $color_black}
.col_deepgray {color : $color_deepgray}
