//"Nanum Gothic",
//h1~h6
$headingFont        : "Noto Sans","맑은고딕","malgun gothic";

@mixin clearfix() {
	display:block;
	&:before,
	&:after{content:"."; display:block; height:0; clear:both; visibility:hidden }
}
@mixin dib() {
	display:inline-block;
	width:auto;
	height:auto;
	margin:0;
	position:static;
	overflow:visible;
	clip:inherit;	
}
@mixin bgpt() {
	background:url(/static/portal/img/sub/pagedeco-pt.png) 0 0 repeat;
	background-size:6px;
}
@mixin dltable() {
	dl{
		dt{
			border-right:1px solid $bdcolor;
			text-align:center;
			width:34%;
			background-color:#f0f2f7;
		}
		dd{}
	}
	dl{
		display:table;
		width:100%;
		table-layout:fixed;
		>*{
			padding:9px 20px;
			display:table-cell;
			float:none;
			vertical-align:middle;
			border-bottom:1px solid $bdcolor;
		}
		&:last-child >*{border-bottom:none;}
	}
}
@mixin maw() {
	max-width:1276px;
	margin: 0 auto;
};