.tab2{
	ul{
		overflow:hidden;
		li{
			float:left;
			@include breakpoint($lg){
				min-width:200px;
				padding-right:1px;
				margin-bottom:1px;
			}
			a{
				display:block;
				background-color:#ebeff8;
				border:1px solid #dee0e6;
				padding:10px;
				text-align:center;
				line-height:1.2;
				color:#000;
			}
			&.on{
				a{
					border-color:#344891;
					background-color:#344891;
					color:#fff;
				}
			}
		}
	}
	& ~ .tab2-body .bu2{margin-top:20px;}
	&.wide{
		ul{
			li{
				width:50%;
			}
		}
	}
}

.tab2-body{
	border-bottom:1px solid $bdcolor;
	padding-bottom:10px;
}
.tab{
	margin-bottom:20px;
	@include breakpoint(max-width 991px){
		background-color:#eef1f7;
		height:51px;
		border-bottom:2px solid #b8c4e4;
		padding:0 52px;
		position:relative;

		.tab-list .owl-nav{
			button.owl-prev,
			button.owl-next{
				width:52px;
				height:49px;
				position:absolute;
				top:0;
				background-size:30px;
				background-repeat:no-repeat;
				background-position:center center;
				span{position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;clip:rect(0,0,0,0);border:0;}
			}
			.owl-prev{
				left:-52px;
				background-image:url(/static/portal/img/common/tab-prev.png);
			}
			.owl-next{
				right:-52px;
				background-image:url(/static/portal/img/common/tab-next.png);

			}
		}
		.item{
			a{
				display:block;
				line-height:47px;
				padding:0 12px;
				color:#000;
				&:hover,
				&:focus{
					font-weight:bold;
					border-bottom:4px solid #20409a;
				}
			}
			&.on a{
				border-bottom:4px solid #20409a;
			}
		}
	}

	@include breakpoint($lg){
		// overflow-x:auto;
		margin-bottom:30px;
		.tab-list{
			@include clearfix();
			.item{
				margin-top:-1px;
				@include breakpoint($lg){
					float:left;
					width:25%;
					padding-right:1px;
					margin-bottom:2px;
				}
				a{
					display:block;
					background-color:#ebeff8;
					border:1px solid #dee0e6;
					padding:10px;
					text-align:center;
					line-height:1.2;
				}
				&.on{
					a{
						border-color:#20409a;
						background-color:#20409a;
						color:#fff;
					}
				}
			}
		}
		// ver 2테이블 타입
