.tabt{
	ul{
		display:table;
		width:100%;
		table-layout:fixed;
		li{
			float:left;
			padding:1px;
			@include breakpoint($md){
				float:none;
				display:table-cell;
				padding:0 0 0 1px;
			}
			a{
				display:block;
				background-color:#ebeff8;
				border:1px solid #dee0e6;
				padding:10px;
				text-align:center;
				line-height:1.2;
				color:#000;
			}
			&.on{
				a{
					border-color:#344891;
					background-color:#344891;
					color:#fff;
				}
			}
		}
	}
}