@mixin bu-dot() {
	background:url(/static/portal/img/common/bu-dot.png) 0 8px no-repeat;
	background-size:5px;
	padding-left:16px;
}
@mixin bu-dot2() {
	background:url(/static/portal/img/common/bu-dot2.png) 0 8px no-repeat;
	background-size:6px;
	padding-left:17px;
}
@mixin bu1() {
	background: url(/static/portal/img/common/bu1.png) 0 center no-repeat;
	background-size : 14px;
	padding-left    : 24px;
	font-size       : 18px;
	font-family     : 'Noto-Regular';
	line-height     : 1;
	margin          : 30px 0 20px;
	color           : #000;
}
@mixin bu2() {
	background: url(/static/portal/img/common/bu2.png) 0 6px no-repeat;
	background-size:6px;
	padding-left:16px;
	line-height:1;
	font-size:18px;
	font-family:'Noto-Medium';
	font-family:'Noto-Regular';
	margin:30px 0 18px 11px;
}
@mixin bu3() {
	background: url(/static/portal/img/common/bu3.png) 0 10px no-repeat;
	background-size:5px;
	padding-left:14px;
	position:relative;
}
@mixin bu4() {
	position:relative;
	padding-left:14px;
	&:before {
		content: '';
		width:3px;
		height:3px;
		background-color:#333333;
		position:absolute;
		left:0;
		top:6px;
	}
}
@mixin bu-dash() {
	text-indent:-12px;
	padding-left:12px;
	&:before {
		content: '- ';
	}
	
}
// 콘텐츠 불릿

/* 1 **************************************************/
#content>h2,
#content>form>h2,
.bu1{
	@include bu1;
	&+p{margin-top:-15px;}
	>span{
		font-size:$defaultfontsize;
		font-weight:normal;
		font-family:$fontSet;
	}
	>a{
		font-weight:normal;
	}
}
#content>h2+p{
	margin:20px 0 15px 20px;
	@include bu3;
	&.n{
		background:none;padding-left:0;
	}
}
/* 2 **************************************************/
#content>h3,
#content>form>h3,
.bu2{
	@include bu2;
	.row &{margin-top:0;}
	&+.n+.list-num,
	&+.list-num
	{
		margin-left:11px;
	}
	>span{
		font-size:$defaultfontsize;
		font-weight:normal;
		font-family:$fontSet;
	}
}
/* 3 **************************************************/
#content>h4,
#content>form>h4,
.bu3{
	@include bu3;
	margin:15px 0 15px 20px;
	font-weight:normal;
	text-indent:0 !important;
	&+.table{margin-top:0;}
}
#content>h3+p{
	@extend .bu3;
	margin-left:20px;
	&.n{
		background:none;padding-left:0;
		margin-left:11px;
	}
}
/* 4 **************************************************/
.bu4{
	@include bu4();
	padding-left:8px;
}
/* 5 **************************************************/
#content>h5
{
	font-size:1em;
}
.bu-dash{@include bu-dash;}
// 좌측 여백
.m1{
	@include breakpoint($md){
		margin-left:$m1;
	}
}
.m2{
	margin-left:$m2;
}
dl.m1{
	*{margin-bottom:5px;}
}

// 리스트타입
.list{
	margin-left:20px;
	>li{
		@include bu3;
		// margin:6px 0;
		>ul{
			>li{
				text-indent:-12px;
				padding-left:12px;
				&:before {
					content: '- ';
				}
				>ul,
				>ol{
					li{
						text-indent:-12px;
						padding-left:12px;
						&:before {
							content: '· ';
							display:inline;
						}
					}
				}

			}
			&.none{
				li{
					&:before {display:none}
				}
			}
			&.number{
				li{
					text-indent:-20px;
					padding-left:20px;
					&:before {display:none}
				}
			}
		}
		.mw{
			margin-left:-20px-14;
		}
	}
	>li+li{
		margin-top:16px;
	}
	.table{margin-top:5px;margin-bottom:0;}
	[class^=b-]{
		min-height:inherit;
		&.b-down{
			margin:-3px 0 0 0;
			padding:4px 8px;
		}
	}
}
dl.list{
	margin:0;
	dt{
		@include bu3;
	}
	dd{
		margin-left:10px;
		text-indent:-16px;
		padding-left:16px;
		&:before {
			content: '- ';
		}
	}
}
.list2{
	margin-top:10px;
	@extend .m1;
	li{ 
		@extend .bu4;
		&+li{margin-top:4px;}
	}
}
@mixin m1(){
	@include breakpoint($md){
		margin-left:$m1;
	}
}
.list-star{
	@include m1;
	margin-top:10px;
	margin-bottom:10px;
	li{
		text-indent:-18px;
		padding-left:18px;
	}
}
.bu-star{
	text-indent:-18px;
	padding-left:18px;	
	color:#20409a;
	font-weight:bold;
	.list+&{
		margin-top:10px;
		@include m1;
	}
}
.list-num{
	h2+p+&,
	h3+p+&,
	h4+&{
		@include m1;
		margin-bottom:10px;
	}
	li{
		padding-left:16px;
		text-indent:-16px;
	}
}
.list-dl{
	@include m1;
	dt{
		@include bu3;
	}
	dd{
		margin-left:10px;
		text-indent:-16px;
		padding-left:16px;
		&:before {
			content: '- ';
		}
	}
	dl+dl,
	dd+dt{
		margin-top:16px;
	}
}
.list-img{
	@include m1;
	img{}
}
p+.table{margin-top:0;}