$regHeight : 30px;
.reg{
	border-top:2px solid $bdtopcolor;
	border-bottom:1px solid $bdtopcolor;
	display:table;
	width:100%;
	table-layout:fixed;
	margin-bottom:10px;
	margin:20px 0;
	position:relative;
	@include breakpoint(768px){
		padding:0;
	}
	&.type2 dl{display:table;}
	
	dl>*{
		display:block;
		border:none;
	}
	dl{
		background-color:#fff;
		display:table-row;
		width:100%;
		table-layout:fixed;
		>*{
			display:table-cell;
			float:none;
			vertical-align:middle;
			border-bottom:1px solid $bdcolor;
		}
	}
	dl{
		dt{
			position:relative;
			color:#222;
			border:none;
			border-right:1px solid #dee0e6;
			border-bottom:1px solid $bdcolor;
			width:105px;
			text-align:center;
			padding-top:14px;
			padding-bottom:14px;
			background-color:#f0f2f7;
			@include breakpoint($md){
				width:125px;
			}
			@include breakpoint($lg){
				width:150px;
			}
		}
		dd+dt:after {
			content: '';
			position:absolute;
			left:0;
			top:50%;
			margin-top:-8px;
			background-color:#e6e6e6;
			width:1px;
			height:16px;
		}
		dd{
			padding:4px 10px;
			p{line-height:1.5;}
		}
		&:last-child > *{border-bottom:none;}
	}
	label{
		margin-right:20px;
		white-space:nowrap;
		&.row{margin-right:0;}
	}
	.row label{margin-right:0;}

	textarea{width:100%;}
	input[type=text],
	input[type=number],
	input[type=date],
	input[type=password],
	select,
	[class^=b-]{
		height:$regHeight;
		min-height:$regHeight;
		padding-top:0;
		padding-bottom:0;
		line-height:$regHeight;
		&.id{
			width:110px;
		}
		&.password{
			width:100%;
			@include breakpoint($md){width:260px;}
		}
		&.post{

		}
	}
	.telSet{
		select{padding-right:0;text-indent:0;padding-left:0;}
		.tel{width:53px;}
		
		@include breakpoint($md){
			display:inline;
			&+p{
				display:inline;
			}

		}
	}
	input[type=text]{
		&.w100p{width:100%;}
		&.post{width:40%;}
	}
	input[type=password]{width:100%;}
	@include breakpoint($md){
		input[type=text]{
			&.post{width:60px;text-align:center;text-indent:0;}
			&.emailId{width:105px}
			&.emailAddr{width:105px}
		}
		input[type=password]{}
	}


	// emailSet
	.emailSet{
		input{width:43%;}
		select{width:100%;margin-top:5px;@include breakpoint($md){width:auto;margin-top:0;}}
	}

	.labelList{
		@include breakpoint($md){
			display:inline;
		}
	}
	.help{
		@include breakpoint($md){
			display:inline;
		}
	}
	textarea+.help{
		display:block;
		margin:10px 0 5px;
	}
}
.regInfor{
	text-align:right;
	&+.reg{margin-top:10px;}
}
.fileAddList{
	>div{margin:5px 0;}
}
.reqInfor{
	@include breakpoint($md){
		float:right;
		margin:-38px 0 20px;
	}
	@include breakpoint($lg){

	}
}
.req{
	&:before {
		content: '*';
		display:inline;
		color:$color_orange;
	}
	span{position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;clip:rect(0,0,0,0);border:0;}
}
.view{
	border-top:2px solid $bdtopcolor;
	border-bottom:1px solid $bdcolor;
	@include breakpoint(768px){
		padding:0;
	}
	.subject{
		border-bottom:1px solid $bdcolor;
		padding:14px 0;
		position:relative;
		font-size:22px;
		font-weight:bold;
		text-align:center;
	}
	dl{
		display:inline-block;
		dt{
			position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;clip:rect(0,0,0,0);border:0;
		}
		dd{
			display:inline-block;
			@include breakpoint($md){
				margin:0 28px;
			}
		}
	}
	.status{
		@include breakpoint($sm){
			overflow:hidden;
			padding:0;
			dl{
				float:left;
				width:50%;
				padding-bottom:15px;
				padding:15px 0;
				>dd{
					padding-left:20px;
					position:relative;
					&:after{
						content: '';
						border-top:1px solid $bdcolor;
						position:absolute;
						left:0;
						top:-16px;
						width:300%;
					}
				}
				&.r{
					border-top:1px solid $bdcolor;
					width:100%;
					overflow:hidden;
					dd{width:50%;float:left;padding-left:20px;}
				}
			}
		}
	}
	.etc,
	.fileSet,
	.status{
		@include breakpoint($md){
			padding:16px;
		}
		color:#000;
		background-color:#f9fafd;
		border-bottom:1px solid $bdcolor;
		position:relative;
		.r{
			@include breakpoint($md){
				position:absolute;
				right:16px-28;
				top:16px;
				text-align:right;
			}
		}
		>dt:first-child+dd,
		dl:first-child dd{margin-left:0;}
	}
	.etc{
		padding:15px 20px;
	}
	.mettingtimes{
		color:#f76627;
		dt{
			@include dib();
			&:before{
				content: '';
				font-family:'LineAwesome';
				display:inline;
			}
		}
	}
	.fileSet{
		display:block;
		@include breakpoint($sm){
			padding:15px;
			a+a{margin-top:5px;}
		}
		a{color:#000;}
	}
	.dbData{
		border-bottom:none;
	}
	>.t{
		border-bottom:1px solid $bdcolor;
		>dl>*{
			display:block;
			border:none;
		}
		@include breakpoint(max-width 767px){
			>dl{
				display:block;
				dt{
					display:block;position:static;width:auto;height:auto;
				}
				>*{padding:15px 20px;border-top:1px solid $bdcolor;}
			}
		}
		@include breakpoint($md){
			>dl{
				display:table;
				width:100%;
				table-layout:fixed;
				>*{
					display:table-cell;
					float:none;
					vertical-align:middle;
					border-bottom:1px solid $bdcolor;
				}
			}
		}
		>dl{
			dt{
				position:relative;
				color:#222;
				background-color:#f9fafd;
				border-left:1px solid $bdcolor;
				&:first-child{border-left:none;}
				&:last-child{border-right:none;}
				@include breakpoint($md){
					border-right:1px solid $bdcolor;
					width:120px;
					text-align:center;
					padding-top:14px;
					padding-bottom:14px;
					@include breakpoint($lg){
						width:200px;
					}
				}
			}
			dd{
				padding-left:25px;
				padding-right:25px;
				&.vab{
					padding-top:10px;
					padding-bottom:10px;
					a{vertical-align:bottom;}
				}
			}
			&:last-child > *{border-bottom:none;}
			@include breakpoint($sm){
				dd+dt{border-left:none;}
			}
		}

	}
}
.view-bd{
	>dl>dt{
		border-right:1px solid $bdcolor;
		&:before {
			display:none;
		}
		@include breakpoint($md){
			width:139px;
		}
	}
}
.view-img{
	.img{margin:30px 0;}
	.img img{
		max-width:100%;
	}
	.img:empty{
		padding-top:76%;
		background:#e6e6e6 url(/static/kspo/img/sub/logogray.png) center center no-repeat;
		background-size:173px auto;
	}
	@include breakpoint($md){
		.img{margin:0}
		.both{
			display:table;
			width:100%;
			table-layout:fixed;
			border-bottom:1px solid $bdcolor;
			>*{
				display:table-cell;
				vertical-align:top;
			}
		}
		.left{
			width:50%;
			padding:30px;
			vertical-align:middle;
			img{max-width:100%;}
		}
		.right{
		}
	}
}
.dbData{
	border-bottom:1px solid #707686;
	padding:16px;
	.bu1+&{border-top:1px solid $bdcolor;}
	@include breakpoint($md){
		// padding:33px-15 60px;
	}
	img{
		max-width:100%;
	}
	&.viewResult{
		background-color:#f3f5f5;
	}
}
.dbimg-list{
	text-align:center;
	img{
		max-width:100%;
		margin-bottom:20px;
	}
}
.dbData2{
	margin:20px 0;
	padding:20px;
	border:1px solid $bdcolor;
	@include breakpoint($md){
		padding:30px;
	}
	img{
		max-width:100%;
	}
	&+.btnSet{
		margin-top:20px;
		padding-top:20px;
		border-top:1px solid $bdcolor;
	}
}

.dbData3{
	padding:20px 0;
	img{
		max-width:100%;
	}
}
.btnSet{
	margin:15px 0;
	&+h2.bu1{margin-top:70px;}
	@include breakpoint($md){
		margin:20px 0;
		.paginationSet+&{
			margin-top:-36px;
		}
	}
	.r{
		@include breakpoint($md){
			text-align:right;
			margin-top:-40px;
		}
	}
}

.view-movie{
	.set{
		overflow:hidden;
		padding:16px 0;
		border-top:1px solid #707686;
		border-bottom:1px solid #707686;
	}
	.subject{
		font-size:16px;
		float:left;
		font-family:'Noto-Regular';
		color:#222;
	}
	.fav{
		font-size:16px;
		float:right;
		span{vertical-align:top;}
	}
	.movieplayer{
		clear:both;
		text-align:center;
		padding:20px 0 12px;
	}
	.divboth .fl{padding-top:5px;}
	a.b-blue{
		padding:8px 25px !important;
	}
	.movie-txt{
		margin-top:12px;
		border-width: 1px;
		border-color: rgb(209, 209, 210);
		border-style: solid;
		background-color: rgb(248, 248, 249);
		padding:10px;
	}
	@include breakpoint($md){
		.b{
			position:relative;
			padding-right:329px;
			min-height:464px;
			.r{
				position:absolute;
				right:0;
				top:20px;
				// padding-left:25px;
				width:304px;
				.movie-txt{
					height:405px;
					overflow-y:auto;
				}
			}
		}
	}
}
.movie-list{
	ul{}
	li{}
	li+li{margin-top:20px;}
	.img{
		position:relative;
		padding-top:56%;
		overflow:hidden;
		display:block;
		img{
			position:absolute;
			left:0;
			width:100%;
			top:0;
			vertical-align:top;
		}
	}
	a{
		display:block;
		.played{
			position: absolute;
			left: 50%;
			top: 50%;
			margin-top: -28px;
			margin-left: -28px;
			background: url(/static/kspo/img/submain/submain6-play@2x.png) center center no-repeat;
			background-size: 56px;
			width: 56px;
			height: 56px;
			z-index: 1;
		}
	}
	span{
		display:block;
	}
	.set{
		border:1px solid $bdcolor;
		padding:12.5px 18.5px;
	}
	.txt{
		font-size:16px;
		border-bottom:1px dotted #b9b9b9;
		padding-bottom:18px;
		margin-bottom:18px;
	}
	.date{}
	@include breakpoint($md){
		border-bottom:1px solid #707686;
		padding-bottom:30px;
		margin-top:-40px;
		ul{
			overflow:hidden;
			margin:0 -16px;
		}
		li{
			float:left;
			width:33.33%;
			padding:0 16px;
			&:nth-child(3n+1){
				clear:both;
			}
		}
		li,
		li+li{margin-top:40px;}
		.img{
			img{
				width:100%;
			}
		}
		a{
			display:block;
		}
		span{
			display:block;
		}
		.set{
			border:1px solid $bdcolor;
			padding:12.5px 18.5px;
		}
		.txt{
			font-size:16px;
			border-bottom:1px dotted #b9b9b9;
			padding-bottom:18px;
			margin-bottom:18px;
			height:67px;
			overflow:hidden;
		}
		.date{}
	}
}
.tableT{
	margin-left:-1px;
	margin-right:-1px;
	width:100%;border-bottom:1px solid #707685;
	table-layout:fixed;
	th,
	td{
		border:1px solid $bdcolor;
	}
	th{
		font-size:15px;font-weight:normal;color:#222;padding:12px 0;border-top:1px solid #707685;font-family:'Noto-Regular';
	}
	td{
		font-size:15px;
		text-align:center;font-family: "Noto-Light", sans-serif;
		color:#656565;
		padding:10px 0;
		border-left:1px solid #e6e6e6;
		border-top:1px solid #e6e6e6;
		&.tal{
			padding-left:20px;
		}
	}
	tbody{
		th{font-family: "Noto-Light", sans-serif;border-top:1px solid #e6e6e6;}
		tr>td:first-child{
			// border-left:none;
		}
		.foot{
			th,
			td{
				background-color:#f8f8f9;
				color:#0a2a70;
				font-family:'Noto-Regular';
				text-align:center !important;
			}
		}
	}
}


.task-board a{font-size:1.14em;padding:0 0 0 5px;color:#141414;font-weight:bold}
.task-board-expanded > *{opacity:0.2;}
.task-board-expanded:hover > *{opacity:0.95;}
.task-board-expanded > .task-board-title{opacity:1;}
div.task-board-recent{border-width:0;}
.task-board{margin-bottom:8px;}

.tagSet{
	border-top:1px solid $bdcolor;
	background:#f9fafd;
	padding:13px 0;
	dt{position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;clip:rect(0,0,0,0);border:0;}
	dd{
		line-height:24px;
		&:before {
			content: '';
			color:#22b24c;
			margin-right:20px;
			font-family:'LineAwesome';
			font-size:24px;
			display:inline-block;
			vertical-align:middle;
		}
	}
	span{
		margin-right:20px;
	}
}

.posi-4,
.posi-2{
	.s{
		position:relative;
		padding-left:70px;
		display:block;
		span{
			position:absolute;
			left:0;
			top:0;
			line-height:$regHeight;
		}
		input{
			width:100%;

		}
	}
}
.posi-2 .s{padding-left:40px;}


.debate{
	background:url(/static/portal/img/sub/pagedeco-pt.png) 0 0 repeat;
	background-size:6px;
	padding:25px 28px 20px;
	border:1px solid $bdcolor;
	dt{
		line-height:30px;
		margin-bottom:7px;
		&:before {
			content: '';
			font-family:'FontAwesome';
			font-size:14px;
			color:#fff;
			background-color:#6e7aa9;
			width:30px;
			height:30px;
			display:inline-block;
			text-align:center;
			border-radius:30px;
			
			margin-right:10px;
		}
	}
	dd{
		position:relative;
		padding-right:113px;
		input[type=text]{
			width:100%;
			height:35px;
			line-height:33px;
			padding:2px 10px;
		}
		input[type=submit]{
			width:100px;
			position:absolute;
			right:0;
			top:0;
			color:#fff;
			background-color:#f76627;
			border:none;
			height:35px;
		}
	}
}
.debate-list{
	border-top:2px solid #344891;
	margin-bottom:80px;
	ul{}
	li{
		padding:18px 30px 13px;
		position:relative;
		border-bottom:1px solid $bdcolor;
	}
	.title{
		strong{
			font-size:18px;
			display:inline-block;
			margin-right:38px;
			line-height:1;
			color:#555;
		}
		.edit{color:#20409a;}
		.del{color:#f76627;}

	}
	.txt{
		margin-top:9px;
	}
	.r{
		position:absolute;
		right:18px;
		top:30px;
		text-align:right;
	}
	.voit{
		color:#f76627;
		white-space:nowrap;
		&:before {
			content: '';
			font-family:'LineAwesome';
		}
		&.up{
			&:before {
			}
		}
		&.down{
			color:#6e7aa9;
			&:before {
				content: '';
			}

		}
	}
	
}