.familySetOpen{
	@include breakpoint($lg){display:none;}
	position: absolute;
	right: 0;
	top: 71px;
	font-family: 'FontAwesome';
	font-size: 20px;
	width: 48px;
	height: 30px;
	z-index:601;
}
.familySetClose{
	display:none;
}
.gnbClose{display:none;}
@include breakpoint($smd){
	.familySetClose,
	.gnbClose{
		// display:block;
		position:absolute;
		right:0;top:0;
		width:54px;height:54px;
		background:#008bce url(/static/mayor/img/main/gnbClose.png) center center no-repeat;
		background-size:23px 21px;
	}
	#gnb>ul{display:none;}
	#gnb.mobileOn{
		.gnbClose{display:block;}
		display:block !important;
		position:absolute;
		left:0;
		top:0;
		width:100%;
		width:320px;
		border-top:none;
		padding:54px 0 0 0;
		margin:0;
		z-index:600;
		&:before {
			content: '';
			position:fixed;
			left:0;
			top:0;
			right:0;
			bottom:0;
			background-color:rgba(0,0,0,0.5);
			z-index:-1;
		}
		&:after {
			content: '';
			background:#fff url(/static/portal/img/common/logo.png) 33px center no-repeat;
			background-size:auto 34px;
			position:absolute;left:0;top:0;right:54px;
			height:54px;
		}
		li{position:relative}
		>ul{
			display:block;
			border:1px solid $bdcolor;
			border-top:2px solid #008bce;
			background-color:#fff;
			>li{
				position:relative;
				>a{
					display:block;
					line-height:1;
					color:#333333;
					padding:17px 0 17px 45px;
					border-bottom:1px solid $bdcolor;
					background:#fff url(/static/mayor/img/main/mNavBu1.png) 18px center no-repeat;
					background-size:19px;
				}
				&.on>a{
					background-color:#f5fcff;
				}
				&.on .dinfoSet>ul,
				&.on .dinfoSet>ul,
				&.on > div .dinfoSet > ul,
				&.on > div .dinfoSet > ul > li.on > div > ul,
				&.hover .dinfoSet>ul{display:block;}
				&.on>div:after{transform:rotate(0);}
				>div{
					&:after,
					&:before{
						content: '';
						width:18px;height:2px;background-color:#008bce;
						position:absolute;right:17px;top:25px;
						transition: all .25s ease-out;
					}
					&:after{transform:rotate(90deg);}
					.img{display:none;}
					.dinfoSet{
						>ul{
							display:none;
							background-color:#f1f1f1;
							>li{
								border-bottom:1px solid $bdcolor;
								position:relative;
								>div{
									&:after,
									&:before{
										content: '';
										width:18px;height:2px;background-color:#757575;
										position:absolute;right:17px;top:25px;
										transition: all .25s ease-out;
									}
									&:after{transform:rotate(90deg);}
									>ul{display:none;}
								}
								&.on>div:after{transform:rotate(0)}
								&.on>ul{display:block;}
								a{
									display:block;
									padding:17px 0 17px 45px;
									background: url(/static/portal/img/common/mNavBu2.png) 22px center no-repeat;
									background-size:13px;
								}
								li a{
									border-top:1px solid $bdcolor;
									background: url(/static/portal/img/common/mNavBu3.png) 35px center no-repeat;
									background-size:6px;
									padding-left:56px;

								}
							}
						}
						li.on>div>ul{display:block;}
						li.on>div:after{transform:rotate(0)}
						.depth4{
							background-color:#fff;
							margin:0 36px;
							
							&:after,
							&:before{
								content: '';
								width:18px;height:2px;background-color:#757575;
								position:absolute;right:17px;top:25px;
								transition: all .25s ease-out;
							}
							&:after{transform:rotate(90deg);}
							ul{
								border:1px solid $bdcolor;
								padding:13px 0;
								display:none;margin-bottom:20px;
							}
							li{}
							a{
								display:block;
								background: url(/static/portal/img/common/mNavBu4.png) 22px center no-repeat;
								background-size:4px;
								border:none;
								padding:2px 0 2px 37px;
								font-size:15px;
							}
						}
					}

				}
			}
		}
	}
	
	#globalWarp .familySet.mobileOn{
		display:block !important;
		position:absolute;
		right:0;
		top:66px;
		width:100%;
		width:320px;
		border-top:none;
		padding:54px 0 0 0;
		margin:0;
		z-index:602;
		.familySetClose{display:block;}
		&:before {
			content: '';
			position:fixed;
			left:0;
			top:0;
			right:0;
			bottom:0;
			background-color:rgba(0,0,0,0.5);
			z-index:-1;
		}
		&:after {
			content: '';
			background:#fff url(/static/portal/img/common/logo.png) 10px 10px no-repeat;
			background-size:auto 34px;
			position:absolute;left:0;top:0;right:54px;
			height:54px;
		}

		>ul{
			border:1px solid $bdcolor;
			border-top:2px solid #008bce;
			>li{
				position:relative;
				float:none;
				
				>a{
					display:block;
					line-height:1;
					color:#333333;
					padding:17px 0 17px 45px;
					border-bottom:1px solid $bdcolor;
					background:#fff url(/static/mayor/img/main/mNavBu1.png) 18px center no-repeat;
					background-size:19px;
				}
				&.on>a{
					background-color:#f5fcff;
				}
				&.on>div>ul,
				// &:hover>div>ul,
				&.hover>div>ul{display:block;}
				&.on>div:after{transform:rotate(0);}
				>div{
					&:after,
					&:before{
						content: '';
						width:18px;height:2px;background-color:#008bce;
						position:absolute;right:17px;top:25px;
						transition: all .25s ease-out;
					}
					&:after{transform:rotate(90deg);}
					>ul{
						display:none;
						background-color:#f1f1f1;
						>li{
							border-bottom:1px solid $bdcolor;
							a{
								display:block;
								padding:17px 0 17px 45px;
								background: url(/static/mayor/img/main/mNavBu2.png) 22px center no-repeat;
								background-size:13px;
							}
						}
					}
				}
			}
		}
	}
}