.bbs_sch_type{
	text-align:right;
	margin-bottom:10px;
	@include breakpoint($md){
		float:right;
		margin-top:-46px;
		margin-bottom:0;
	}
	button{
		font-family:'FontAwesome';
		border:1px solid $bdcolor;
		width:90px;
		height:30px;
		background-color:#fff;
		&.on{
			background-color:#6274b2;
			border-color:#6274b2;
			color:#fff;
		}
		&:before {content: ' ';display:inline;}
		&.type-photo:before {content: ' '}
	}
}
.thumList{
	border-top:3px solid $bdtopcolor;
	border-bottom:1px solid $bdcolor;
	padding-bottom:20px;
	ul{
		overflow:hidden;
		margin-left:-13px;
		margin-right:-13px;
		@include breakpoint($md){
		}
		li{
				float:left;
				width:50%;
				padding:0 13px;
				margin-top:10px;
			@include breakpoint($md){
				width:25%;
				margin:20px 0 0;
			}
			@include breakpoint($lg){
				width:25%;
			}
			a{
				.s{
					padding:8px 9px 0 9px;
					.vline{display:inline-block;font-size:9px;vertical-align:top;margin-top:3px;}
				}
				span{
					display:block;

				}
				.img{
					height:166px;
					display: flex;
					flex-direction: row;
					justify-content: center;
					align-items: center;
					border:1px solid $bdcolor;
					img{max-width:100%;max-height:164px;}
				}
				.tit{
					color:#000;
					text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
				}
				[class^=ico-]{
					color:#757575;
				}

			}
		}
	}
	&.type-title{
		ul{
			overflow:hidden;
			margin:0;
			li{
				float:none;
				width:100%;
				padding:0;
				margin-top:10px;
				@include breakpoint($md){
					width:auto;
					margin:20px 0 0;
				}
				@include breakpoint($lg){
					width:auto;
				}
				a{
					display:block;
					overflow:hidden;
					// padding-left:150px+20;
					position:relative;
					display:table;
					table-layout:fixed;
					width:100%;
					.s{
						padding:0 0 0 20px;
						@include breakpoint($lg){
							padding:0 0 0 28px;
						}
					}
					>span{display:table-cell;vertical-align:middle;}
					.img{
						width:150px;
						vertical-align:top;
						height:auto;
						text-align:center;
						img{
							max-height:166px;
						}
						
						@include breakpoint($md){width:230px;}
					}
					.tit{
						font-size:16px;
						margin-bottom:12px;
						white-space:normal;
					}
					.ico-dept{margin-bottom:8px;}
					[class^=ico-]{
						color:#757575;
					}
				}
			}
		}
	}
	&.type-map{
		ul{
			overflow:hidden;
			margin:0;
			li{
				position:relative;
				float:none;
				width:100%;
				padding:0;
				margin-top:10px;
				@include breakpoint($md){
					padding:0 150px 0 0;
					width:auto;
					margin:20px 0 0;
				}
				@include breakpoint($lg){
					width:auto;
				}
				a{
					display:block;
					overflow:hidden;
					// padding-left:150px+20;
					position:relative;
					display:table;
					table-layout:fixed;
					width:100%;
					.s{
						padding:0 0 0 20px;
						@include breakpoint($lg){
							padding:0 0 0 28px;
						}
						>span{
							@include bu3;
							&.tit{background:none;padding-left:0;}
						}
					}
					>span{display:table-cell;vertical-align:middle;}
					.img{
						width:150px;
						vertical-align:top;
						height:auto;
						
						@include breakpoint($md){width:230px;}
					}
					.tit{
						font-size:16px;
						margin-bottom:12px;
						white-space:normal;
					}
					.ico-dept{margin-bottom:8px;}
					[class^=ico-]{
						color:#757575;
					}
				}
				a.b-map{
					background-color:#6e7aa9;
					padding-top:10px;padding-bottom:10px;
					@include breakpoint($md){
						position:absolute;
						right:0;
						bottom:0;
						width:130px;
					}
				}
			}
		}
	}
}
.thumAList{
	border-top:3px solid $bdtopcolor;
	border-bottom:1px solid $bdcolor;
	ul{}
	li{
		@include clearfix;
		padding-left:120px;
		position:relative;
		margin-top:20px;
		padding-bottom:20px;
		border-bottom:1px solid $bdcolor;
		margin-bottom:-1px;
		.img{
			position:absolute;
			left:0;
			width:100px;
			height:136px;
		}
		.s{
			span{
				display:block;
				margin-bottom:4px;
			}
			.tit{
				font-size:18px;
				font-weight:bold;
				line-height:1;
				margin-bottom:16px;
				padding-right:20px;
				// text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
			}
		}
	}
	@include breakpoint($md){
		ul{
			overflow:hidden;
		}
		li{
			padding-left:220px;
			float:left;
			width:50%;
			border:none;
			.img{
				position:absolute;
				left:0;
				width:200px;
				height:136px;
			}
			&:nth-child(odd){
				clear:both;
				&:before {
					content: '';
					position:absolute;
					left:0;
					top:-20px;
					width:200%;
					border-top:1px solid $bdcolor;
					visibility:visible;
				}
			}
		}
	}
}