$btn:"[class^=b-]";
#{$btn}{
	text-indent:0 !important;
	display:inline-block;
	min-height:$formHeight;
	padding: ($formHeight - 2 - $defaultfontsize) / 2 5px;
	font-size:$defaultfontsize;
	line-height:1;
	text-align:center;
	color:#fff;
	background-color:$btnbgcolor;
	border:none;
	border-radius:0;
	vertical-align:middle;
	@include breakpoint($md){
		min-width:88px;
		// padding: ($formHeight - 2 - $defaultfontsize) / 2 0;
	}
	&:hover,
	&:focus{
		text-decoration:underline;
	}
	&.xl{
		padding:18px 0;
		width:290px;
	}
	&.lg{
		padding:18px 30px;
		font-size:18px;

	}
	&.sm{
		padding:5px;
		min-height:inherit;
	}
	&.green{
		background-color:#22b24c !important;
	}
	&.w{
		@include breakpoint($md){
			width:140px;
		}
	}
}

#{$btn}.b-mapview{
	border:1px solid #dee0e6;text-align:center;background-color:transparent;text-indent:0;
	padding:6px 8px;
	min-height:inherit;
}
#{$btn}.b-line{
	color:$color_blue;
	border:1px solid $color_blue;
	background-color:transparent;text-indent:0;
}
#{$btn}.b-map{
	padding:6px 8px;
	min-height:inherit;
	&:after {
		content: '';
		font-family:'FontAwesome';
		display:inline;
		margin-left:5px;
	}

}
#{$btn}.b-print{
	@extend .b-mapview;
	color:#141414;
}
#{$btn}.b-notify{background-color:#f76627}
#{$btn}.b-homepage{background-color:#20409a}
#{$btn}.b-app{background-color:#22b24c;}
#{$btn}.b-lview{
	padding-left:30px;
	padding-right:30px;
	&:before {
		content: '';
		width:15px;
		height:15px;
		background:url(/static/portal/img/common/ico-lageview.png) 0 0 no-repeat;
		background-size:15px;
		margin-right:10px;
		display:inline-block;		
	}
}
#{$btn}.b-down{
		background-color:$btnbgcolor;
		padding-left:10px;
		padding-right:10px;
	p>&{
		margin-top:-10px;
		margin-bottom:-10px;
	}
	
	@include breakpoint($md){
		padding-left:29px;
		padding-right:29px;
	}
	&:after {
		content: '';
		font-family:'FontAwesome';
		display:inline;
		margin-left:5px;
	}
	&.sm{
		@include breakpoint($md){
			padding-left:5px;
			padding-right:5px;
		}
	}
}
#{$btn}.b-detailview{
	@extend .b-down;
	&:after {
		content: '';
		font-family:'LineAwesome';
		font-size:22px;
		line-height:15px;
	}
}
#{$btn}.b-sitego{
	background-color:$btnbgcolor;
	padding-left:40px;
	padding-right:40px;
	&:after {
		content: '';
		font-family:'LineAwesome';
		font-size:24px;
		line-height:15px;
	}
	td &{
		padding-left:4px;
		padding-right:4px;
		text-indent:0;
	}
	&.s{
		padding-left:8px;
		padding-right:8px;
	}
	p>&{
		margin-top:-10px;
		margin-bottom:-10px;
	}
}
i#{$btn}.b-sitego2{padding:0;}
#{$btn}.b-sitego2{
	// background-color:$btnbgcolor;
	// color:$btnbgcolor;
	font-style:normal;
	color:#22b24c;
	background-color:transparent;
	padding-left:5px;
	padding-right:5px;
	min-width:auto;
	&:after {
		content: '';
		font-family:'LineAwesome';
		font-size:24px;
		line-height:15px;
	}
}
#{$btn}.b-total{background-color:$btnbgcolor;}
#{$btn}.b-chkAll{background-color:$btnbgcolor;}
#{$btn}.b-write{background-color:#fff;color:#000;border:1px solid $bdcolor;}
#{$btn}.b-del{background-color:#7f7f7f}
#{$btn}.b-gray{background-color:#5e5f67;}
#{$btn}.b-gray2{background-color:#787e96;}
#{$btn}.b-cancel{background-color:#fff;color:$color_default;border:1px solid $bdcolor;}
#{$btn}.b-view{font-size:0.9em;padding-left:8px;padding-right:8px;text-indent:0;}

#{$btn}.b-submit{
	// 글쓰기
	background-color:#4c5580;
}

