#{$btn}.b-join{
	@extend .b-lg;
	background-color:#fff;
	color:$btnbgcolor;
	background: url(/static/ucms/img/ico-arrRight.png) 152px center no-repeat;
	width:180px;
	border:1px solid #000;
	text-align:left;
	height:50px;

}
#{$btn}.b-lg{
	padding:16px 35px;
	font-size:18px;
	font-weight:bold;
	font-family:'Nanum Square';
}
#{$btn}.b-arr{
	margin-left:10px;
	border:1px solid #000;
	padding:15px 67px 15px 20px;
	line-height:1;
	background:#fff ;
	color:#141414;
	position:relative;
	&:after {
		content: '';
		width:8px;
		height:14px;
		background: url('/static/ucms/img/ico-arrRight.png') right center no-repeat;
		position:absolute;
		right:20px;
		top:0;
		bottom:0;
		margin: auto;
		
	}
}
.url-link{
	margin:20px 0;
	background:#e7ebec url(/static/ucms/img/ico-doc.png) 20px center no-repeat;
	line-height:1;
	padding:12px 50px 12px 45px;
	color:$btnbgcolor;
	font-size:16px;
	font-weight:bold;
	display:inline-block;
	border-radius:5px;
	box-shadow: 1px 2px 0 0 hsla(0,0%,0%,.25);
	position:relative;
	&:after {
		content: '';
		position:absolute;
		right:20px;
		top:13px;
		width:8px;
		height:14px;
		background: url(/static/ucms/img/ico-arrRight.png) 0 0 no-repeat;
	}
	&.blank:after,
	&[target=_blank]:after{
		// 새창
	}
}

// 
[class*=ico-]{
	&:before {
		display:inline-block;
		font-style:normal;
		margin-right:6px;
		font-family:'LineAwesome';
	}
	&.ico-dept:before{content: '';}
	&.ico-date:before{content: '';}
	&.ico-view:before{content: '';font-family:'FontAwesome';}
	&.ico-name:before{}
	&.ico-tel:before{content:'';font-family:'FontAwesome';}
	&.ico-air:before{content:'';font-family:'FontAwesome';}
	&.ico-fax:before{content:'';font-family:'FontAwesome';margin-right:3px;}
}
.go{
	background: url(/static/portal/img/common/ico-sitego.png) 0 0 no-repeat;
	background-size:17px;
	width:17px;
	height:17px;
	display:inline-block;
}
.quickview{
	line-height:1;
	vertical-align:middle;
	span{
		padding:6px 15px;
		display:inline-block;
		border-radius:3px;
		border:1px solid #000;
	}
}
.imgview{
	padding:6px 22px;
	border:1px solid #424f88;
	color:#424f88;
	display:inline-block;
	line-height:1;
	vertical-align:middle;
	border-radius:3px;
	&:before {
		margin-right:10px;
		content: '';
		background:url(/static/portal/img/common/ico-img.png) 0 0 no-repeat;
		background-size:14px;
		display:inline-block;
		width:14px;height:14px;
	}
}


