.table{
	position:relative;
	border-top:3px solid $viewbdtopcolor;
	margin-top:30px;
	margin-bottom:15px;
	overflow-x:auto;
	-webkit-overflow-scrolling: touch;
	h2+&,
	h3+&,
	h4+&{margin-top:0;}
	&:before {
		content: '';
		height:100%;
		border-right:1px solid #fff;
		position:absolute;
		top:0;
		right:0;
	}
	&.w table{
		table-layout:auto;
		// min-width:inherit;
	}
	&.tal{
		table{
			td{
				text-align:left;
			}
		}
	}
	table{
		min-width:700px;
		// width:99.99%;
		width:100%;
		table-layout:fixed;
		text-align:center;
		background-color:#fff;
		th,
		td{
			border-right:1px solid $bdcolor;
			border-bottom:1px solid $bdcolor;
			border-top:none;
		}
		th{
			padding:12px 3px;
			background-color:$viewheadBgcolor;
		}
		td{
			color:#656565;
			padding:10px 3px;
			@include breakpoint($md){
				padding-left:20px;
				padding-right:20px;
			}
			>div,
			>p{
				text-indent:-12px;
				padding-left:12px;
				*{
					text-indent:0;
				}
			}
		}
		tbody{
			tr:first-child>td{
				border-top:none;
			}
			.foot{
				th,
				td{
					background-color:#ebebeb;
					font-weight:bold;
				}
			}
			.bgcolor{background-color:#f0f2f7;}
		}
	}
	&.bd{
		table th,
		table td{
			border:1px solid $bdcolor !important;
		}
		table tbody tr:first-child>*{
			border-top:none !important;
		}
	}
	&.bd2{
		table th,
		table td{
			// border:1px solid $bdcolor !important;
		}
		table thead th{
			border-bottom:1px solid #cccccc !important;
			&+th{
				border-left:1px solid $bdcolor;
			}
		}
		table tbody th{
			color:#222;
		}
		table tbody tr:first-child>*{
			border-top:none !important;
		}
	}
	&.td-tal{
		table td{text-align:left;padding-left:20px;}
	}
	&.type2{
		table thead th{
			border-left:1px solid $bdcolor;
			border-bottom:1px solid $bdcolor;
			text-align:center;
			// &:first-child{border-left:none;}
		}
		table td{
			text-align:left;
		}
		table tbody th{text-align:center;}
		// table tbody tr:last-child th{border-bottom:1px solid #707685;}
		table tbody td{padding-left:15px;padding-right:15px;}
		table tbody tr:first-child>*{border-top:none;}
	}
	&.tac{
		table td{
			text-align:center;
		}
	}
	&.type3{
		table th{
			border-left:1px solid $bdcolor;
			border-bottom:1px solid $bdcolor;
			text-align:center;
			&:first-child{border-left:none;}
		}
		table td{
			text-align:center;
		}
		table tbody th{text-align:left;padding-left:20px;}
		table tbody tr:last-child th{border-bottom:1px solid #707685;}
		table tbody td{
			padding-left:15px;padding-right:15px;
		}
		table tbody tr:last-child th,
		table tbody tr:last-child td,
		table tbody tr td.bd-b{
			// border-bottom:1px solid #707685;
		}
		table tbody tr:first-child>*{border-top:none;}
		margin-bottom:-1px;
		border-bottom:1px solid #707685;
	}
	&.s{
		table{
			font-size:13px;
			tbody td{padding:5px 3px;}
		}
	}
	&.m{
		table tbody td{padding:5px;}
	}
	&.w{
		overflow-x:auto;
		table{
			
		}
		@include breakpoint($lg){
			overflow-x:hidden;
		}
	}
	&.w2{
		overflow-x:auto;
		table{
			min-width:750px;
			tbody td{
				padding-left:4px !important;
				padding-right:4px !important;
			}
		}
		@include breakpoint($lg){
			overflow-x:hidden;
		}
	}
	&.ma{
		@include breakpoint(max-width 768px){
			table th{width:auto!important}
		}
	}
	&.type-wide{
		table tr td{
			padding-left:4px;padding-right:4px;
			@include breakpoint($md){
				padding-left:12px;padding-right:12px;
			}
			a{
				display:block;
				padding:5px 10px;
				height:auto;
				min-height:auto;
				&[class*=b-]{
					line-height:1;
					padding:8px 8px;
					letter-spacing:-1px;
				}
			}
		}

	}
	.bu3{
		margin:0;
	}
	.list{margin-left:0;}
	
}
.table.type-row{
	tbody tr:first-child>*{
		border-top:1px solid #707685;
	}
	tbody{
		tr>td{}
		tr>td:first-child{border-left:1px solid $bdcolor}
		tr>td:last-child{border-right:none;}
	}
}
.table-rinfor,
.table-rtdeco{
	text-align:right;
	background:none !important;
	padding:0;
	#content>h2+&{margin:-1.5em 0 0 0;}
	#content>h3+&{margin:-1.5em 0 0 0;}
	&+.table{margin-top:10px;}
}
.table-rtdeco2{
	margin-top:-5px;
	margin-bottom:10px;
	@include breakpoint($md){
	text-align:right;
	margin-top:-44px;
	margin-bottom:15px-9;
	}
}
.table-rtdeco3{
	position:relative;
	@include breakpoint($md){
		a{
			position:absolute;
			right:0;
			bottom:0;
		}
	}
}