/* 테이블 */
.bdList{
	margin-top:15px;
	border-top:2px solid $bdtopcolor;
	ul{}
	li{
		border-bottom:1px solid $bdcolor;
		color:#757575;
		@include breakpoint($sm){
			padding:12px 0;
			padding-left:60px;
			position:relative;
			.num{
				width:60px;
				position:absolute;
				left:0;
				text-align:center;
				top:50%;
				margin-top:-10px;
			}
		}
		@include breakpoint($md){
			text-align:center;
		}
		>*{}
		&:hover,
		&:focus{
			background-color:#fafbfd;
		}
	}
	.num{color:#000;}
	.s{
		text-align:left;
		>a{
			.cate{
				color:#424f88;
				display:block;
			}
			.tit{
				display:block;
				color:#555;
				font-size:17px;
				letter-spacing:-1px;
				font-weight:bold;
				padding:3px 0 4px;
				&.normal{
					font-weight:normal;
					font-size:1em;
					padding:0;
				}
			}
		}
		.vline{
			display:inline-block;
			height:14px;
			overflow:hidden;
			border-left:1px solid #d5d5d5;
			color:transparent;
			vertical-align:middle;
			margin-right:2px;
			margin-left:5px;
		}
		i~i{margin-left:10px;}
		
	}
	.dept{
		&:before {
			content: '';
			font-family:'LineAwesome';
			display:inline;					
			margin-right:6px;
		}
	}
	.date{
		&:before {
			content: '';
			font-family:'LineAwesome';
			display:inline;					
			margin-right:6px;
		}
	}
	// 진행중, 마감
	.status{
		span{
			background-color:#344891;
			border-radius:3px;
			color:#fff;
			display:inline-block;
			padding:3px;
			text-align:center;
			@include breakpoint($md){
				width:80px;
			}
		}
		&.end{
			span{
				background-color:#7f7f7f;
			}
		}		
	}
	// 
	.metting{
		line-height:1;
		display:inline-block;
		padding:8px 20px;
		background-color:#6e7aa9;
		margin-right:15px;
		color:#fff;
		@include breakpoint($md){margin-right:35px;}
		&.wait{background-color:#5e5f67} 
		&.close{background-color:#777c8a} 
	}
	.ydm{

	}
	.ico-name{
		&:before {
			content: '';
			font-family:'LineAwesome';
		}
	}
	.ico-lock{
		&:before {
			content: '';
			color:#22b24c;
			margin-right:38px;
			font-size:22px;
		}
	}
	.ico-unlock{
		@extend .ico-lock;
		&:before {
			content: '';
			color:#516dbc;
		}
	}
	.app{
		text-align:center;
		color:#fff;
			width:88px;
		span{
			width:80px;
			display:inline-block;
			padding:5px 10px;
			line-height:1;
			border-radius:3px;
			background-color:#6e7aa9;
		}
		&.close,
		&.end{
			span{background-color:#5e5f67;}
		}
	}
	.add{
		width:180px;
		a[class^=b-]{width:150px;padding-left:0;padding-right:0;}
	}

	@include breakpoint($md){
		ul{
			display:table;
			width:100%;
			table-layout:fixed;
			li{
				display:table-row;
				>*{
					display:table-cell;
					vertical-align:middle;
					border-bottom:1px solid $bdcolor;
					padding:15px 0;
				}
			}
		}
		.num{width:90px;}
		.s{
			>a{
				.cate{}
				.tit{}
			}
		}
		.dept{width:15%;}
		.date{width:14%;}
		.status{
			width:10%;
		}
	}
	table{
		width:100%;
		table-layout:fixed;
		text-align:center;
		td+td,
		th+th{border-left:1px solid $bdcolor;}
	}

	// th:first-child,
	// td:first-child {padding-left:20px; }
	thead{
		th{
			height:50px; 
			font-size:16px;
			color:#163d40;
			font-weight:bold;
			border-bottom:1px solid $bdcolor;
			position:relative;
			background-color:#f0f2f7;
			// &:before {
			// 	content: '';
			// 	position:absolute;
			// 	left:0;
			// 	top:50%;
			// 	margin-top:-8px;
			// 	height:16px;
			// 	border-left:1px solid $bdcolor;
			// }
			// &:first-child:before{display:none;}
			&.no{width:80px;}
			&.file{width:80px;}
			&.name{width:100px;}
			&.date{width:130px;}
			&.count{width:80px;}
		}

	}
	tbody{
		th,
		td{
			font-family:'Noto-Light';
			font-weight:normal;
			border-bottom:1px solid $bdcolor;
			@include breakpoint($md){
				height:44px;
			}
		}
		th{
			color:#222;
		}

	}
	td a {color:#666; line-height:1.2;}
	.notice td {font-size:16px; color:#0a2a70; border-bottom:1px solid $bdtopcolor; background:#f2f5fb;}
	.tit,
	.title {text-align:left;}
	.tit a{
		display:block;
		text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
		line-height:1.47;
	}
	.num {}
	@include breakpoint(max-width 767px){
		td.pc{display:none;}
		table{display:block;}
		thead{display:none;}
		tbody{display:block;}
		tr{
			position:relative;
			display:block;
			padding:20px 0 20px 44px;
			border-bottom:1px solid #e6e6e6;
			line-height:2.07;
			&.nodata,
			&.notice{
				padding:10px;
				background:#f2f5fb;
				td{
					padding:0 10px 0 0;
					border:none;
					&.num{
						position:static;
					}
				}
			}
		}
		&.no-num tr{padding-left:0;}
		tbody th,
		tbody td{
			display:inline-block;
			border:none;
			padding:0 10px 0 0;
			&:first-child{padding-left:0;}
			&.num{
				position:absolute;
				left:5px;
				top:20px;
				padding:0;
			}
			&.title{display:block;padding:0;
				a{text-overflow:ellipsis;overflow:hidden;white-space:nowrap;display:block;
					// &:before{
					// 	content: '';
					// 	width:100%;
					// 	height:100%;
					// 	position:absolute;
					// 	left:0;top:0;
					// }
				}
			}
			&.btn,
			&.m-block{
				display:block;
				text-align:left;
			}
			&.mc{
				color:#222;
				font-family:'Noto-Regular';
			}
		}
		&.type2{
			tr{
				padding:10px 0;
			}
			th,td{display:block;text-align:left;padding:0;}
			th{
				font-size:16px;
				font-family:'Noto-Regular';
				color:#222;
				// margin-bottom:10px;
			}
			td{
				// margin-bottom:6px;
				&:last-child{margin-bottom:0;}
			}
		}
	}
	@include breakpoint($md){
		.fix-notice td {font-size:16px; color:#0a2a70; border-bottom:1px solid $bdtopcolor; background:#f2f5fb;}
		&.bd td{
			border-left:1px solid #e6e6e6;
			&:first-child{border-left:none;}
		}
		td.btn>*{width:100%;min-width:0;}
	}
}
.totalCount{
	margin:28px 0 18px 18px;
	font-size:0.93em;
	font-weight:bold;
	line-height:1;
	strong{
		color:#ee7d5e;
		font-weight:normal;
	}
}

.nodata{
	text-align:center;
	padding:20px 0;
	background:#f0f2f7;
	border-bottom:1px solid $bdcolor;
}

.fileList{
	@include breakpoint($sm){
		padding:5px 0;
	}
	a{
		display:inline-block;
		color:#757575;
		line-height:1.27;
		margin-right:33px;
		&:before{
			content: '';
			background: url(/static/portal/img/common/ico-file.png) 0 0 no-repeat;
			width:18px;
			height:19px;
			background-size:100%;
			display:inline-block;
			vertical-align:top;
		}
		>span{
			display:inline-block;
			vertical-align:top;
		}
		.orignlFileNm{
			max-width:140px;
			text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
			@include breakpoint(360px){max-width:190px}
			@include breakpoint(480px){max-width:300px}

		}
		.fileExtsnNm{
			@include breakpoint($md){
			}
		}
	}
}