$bdcolor : #ccc;
button{border:none;}
/* 셀렉트 */
input[type=text]::-ms-clear{display:none;}
input{vertical-align:middle;}
select{
	padding:5px 8px;
	color:$color_default;
	box-sizing:border-box;
	border:1px solid $bdcolor;
	border-radius:0;
	// -webkit-appearance: none;
	min-height:$formHeight;
	line-height:1;
	vertical-align:middle;
}
/* input */
input[type="date"],
input[type="email"],
input[type="number"],
input[type="text"],
input[type="password"]{
	color:$color_default;
	border:1px solid $bdcolor;
	border-radius:0;
	// font-size:15px;
	line-height:1;
	height:$formHeight;
	@include breakpoint($md){
		text-indent:10px;
	}
}
input[type="password"]{font-family:'맑은 고딕'} //폰트설정시 패스워드가 특수기호라서 글자가 나오지 않음 
input[type=date] {
	text-indent:0;text-align:center;
  &::-webkit-datetime-edit-month-field {
    // color: red;
  }
  &::-webkit-datetime-edit-day-field {
    // color: white;
  }
  &::-webkit-datetime-edit-year-field {
    // color: blue;
  }
  &::-webkit-clear-button {
    font-size: 18px;
    height: 30px;
    position: relative;
    right: 5px;
    margin-right: 4px;
    display:none;
  }
  &::-webkit-inner-spin-button {
    height: 50px;
    display:none;
  }
  &::-webkit-calendar-picker-indicator {
    font-size: 18px;
    &:hover {
      background-color: lighten(#2f2f2f,40%);
      color: darken(#fff, 10%);
      cursor: pointer;
    }
    &:active {
      color: blue;
    }
  }
}

textarea{
	border:1px solid $bdcolor;
	border-radius:0;
	height:247px;
}
.tel{
	width:50px;
	@include breakpoint($md){
		width:70px;
	}
}

