input[type=radio],
input[type=checkbox]{
	// position: absolute;
	// opacity:0;
	z-index:1;
	width:20px;
	height:20px;
	cursor:pointer;
	vertical-align:middle;
	&~i{
		position:relative;
		display:inline-block;
		vertical-align:middle;
		margin-left:-14px;
		border:1px solid #cdcdcd;
		height: 14px;
		width: 14px;
		background-color:#f6f6f6;
		margin-right:5px;
		&::before {
			display: block;
			position: absolute;
			content: '';
			left:2px;
			top:3px;
			}
	}
	&:checked ~ i{
		&::before {
			// @include sprite($ico-check);
			content: "\f14a";
			font:normal normal normal 14px/1 FontAwesome;
			}
	}
	&:focus~i{
		border:1px dotted;
	}
}


input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button
{
  -webkit-appearance: none;
  margin: 0;
}

input[type=number]
{
  -moz-appearance: textfield;
}