.pagedeco{
	@include bgpt();
	margin-bottom:15px;
	@include breakpoint($md){
		margin-bottom:30px;
	}
	dl{
		padding:20px;
		dt{
			// font-family:'Noto-Light';
			// font-family:'Noto-Regular';
			margin-bottom:4px;
			color:#20409a;
			font-size:1.2em;
			font-weight:bold;
		}
		@include breakpoint($md){
			padding:32px 40px 32px 188px;
			min-height:140px;
			background:url(/static/portal/img/sub/200236.png) 40px 24px no-repeat;
			background-size: auto 93px;
			dt {font-size:18px;letter-spacing:-1px;}
		}
		.bu3{margin-left:0;}
		.small{font-size:15px;}
	}
	.list{margin-left:0;font-size:$defaultfontsize;li{margin:0;}}
	&.bd{
		background:none;
		border:5px solid #e6e8ea;
		@include breakpoint($md){
			dl{padding-left:183px}
		}
	}
	&.l{
			display:table;
			width:100%;
			@include breakpoint($md){min-height:140px;}
		>*{
			display:table-cell;
			vertical-align:middle;
			padding:20px;
			strong{
				color:#20409a;
				font-weight:bold;
				font-size:18px;
			}
			@include breakpoint($md){
			padding:32px 40px 32px 188px;
			background:url(/static/portal/img/sub/200236.png) 40px 24px no-repeat;
			background-size: auto 93px;

			}
		}
	}
